.catalog__main-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.catalog__wrapper {
    list-style: none;
}

.catalog__list {
    margin: 0;
    list-style: none;
    padding-left: 0;
}

.catalog__item {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 24px;
    width: $width;
    border-bottom: 1px solid #d6d6d6;
}

.catalog__rate-wrapper {
    display: flex;
    justify-content: flex-start;
    align-items: baseline;
    gap: 16px;
    padding-top: 16px;
    padding-bottom: 25px;
    padding-right: 217px;
}

.catalog__icon {
    min-height: 25px;
    min-width: 25px;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/like-icon.svg");
}

.catalog__rate {
    font-weight: 700;
    font-size: 14px;
    line-height: 21px;
}

.catalog__title {
    padding-top: 15px;
    padding-left: 0;
    padding-right: auto;
}