.form__legend {
    margin: 0;
    padding-top: 24px;
    padding-bottom: 24px;
}

.form__name {
    margin: 0;
    border: none;
    display: flex;
    flex-direction: column;    
    min-width: 280px;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: uppercase;
    text-align: center;
    padding-left: 20px;
    padding-right: 20px;
}

.form__phone-icon {    
    min-height: 58px;
    min-width: 58px;          
    background-color: #e6e6e6;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/phone.svg");
}

.form__email-icon {    
    min-height: 58px;
    min-width: 58px;          
    background-color: #e6e6e6;
    background-repeat: no-repeat;
    background-position: center;
    background-image: url("../img/mail.svg");
}

.input__wrapper {
    display: flex;
    justify-content: flex-end;
}

.form__name:nth-last-child(n+3) {
    padding-bottom: 39px;
    border-bottom: 1px solid #d6d6d6;
}

.form__list {
    padding-left: 0;
    margin: 0;
}

.form__item {
    margin: 0;
    list-style: none;
    padding-left: 0;
}

.form__label {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;
    padding-left: 16px;
}


.form__label-check {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: inherit;
    font-weight: 700;
    font-size: 18px;
    line-height: 26px;
    text-transform: capitalize;
}

// check-box
.form__input {    
    width: 44px;
    height: 44px;
    margin-left: -15px;     
}

.form__item-mark {    
    position: absolute;
    top: 2px;
    left: -1px;
    width: 46px;
    height: 46px;
    border-radius: 4px;
    background-color: $secondary;
    border: 2px solid $secondary;
}

.form__label-check {
    display: flex;
    justify-content: flex-start;
    gap: 16px;   
}

