.form__input[type="checkbox"]:checked+.form__item-mark::before,
.form__input[type="checkbox"]:checked+.form__item-mark::after {
    position: absolute;
    top: 8px;
    left: 11px;
    width: 0;
    height: 2px;
    background-color: #333333;
    content: url("../img/tick.svg");
}

// radio
.form__input[type="radio"] + .form__item-mark {
    border-radius: 50%;
}

.form__input[type="radio"]:checked+.form__item-mark::before {
    position: absolute;
    top: 13px;
    left: 13px;
    width: 16px;
    height: 16px;
    background-color: #333333;
    border-radius: 50%;
    content: "";
}

// button
.form__feedback-button {
    background-color: $primary;
    width: 100%;
    min-height: 58px;
    margin-top: 56px;
}

// info
.form__note {
    display: flex;
    justify-content: flex-end;
    font-weight: 400;
    font-size: 14px;
    line-height: 21px;
    text-transform: uppercase;    
}